import React, { useEffect } from "react";
import { useState } from "react";

import axios from "axios";
import "./App.css";

function App() {
  const [coins, setCoins] = useState([]);

  const getData = async () => {
    axios
      .get("/api")
      .then((res) => {
        setCoins(res.data);
      })
      .catch((error) => console.log(error));
  };

  useEffect(() => {
    getData();
    setInterval(async () => {
      getData();
    }, 10000);
  }, []);

  return (
    <div className="App">
      <div className="other-crypto">
        <div className="box">
          <img src="./logo/btc.png" className="logo" />
          <div className="text">
            <h4>Bitcoin (BTC)</h4>
            <h2>${coins && coins.data?.BTC.quote.USD.price.toFixed(3)}</h2>
          </div>
        </div>
        <div className="box">
          <img src="./logo/eth.png" className="logo" />
          <div className="text">
            <h4>Ethereum (ETH)</h4>

            <h2>${coins && coins.data?.ETH.quote.USD.price.toFixed(3)}</h2>
          </div>
        </div>
        <div className="box">
          <img src="./logo/xrp.png" className="logo" />
          <div className="text">
            <h4>Ripple (XRP)</h4>
            <h2>${coins && coins.data?.XRP.quote.USD.price.toFixed(3)}</h2>
          </div>
        </div>
      </div>
      <div className="box-container">
        <div className="box-enterbutton">
          <img src="./logo/entc.png" className="logo-entc" />
          <div className="text-entc">
            <h2>EnterButton (ENTC)</h2>
            <h1>${coins && coins.data?.ENTC.quote.USD.price.toFixed(3)}</h1>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
